import {Flex, IconButton, Menu, MenuButton, MenuItem, MenuList, useDisclosure} from "@chakra-ui/react";
import {ChatMessages} from "./ChatMessages";
import {Chatbox} from "./Chatbox";
import {useRef, useState} from "react";
import {CommandHistory} from "./CommandHistory";
import {HamburgerIcon} from "@chakra-ui/icons";

export function Chatbot() {
    const [messages, setMessages] = useState([])
    const {isOpen, onOpen, onClose} = useDisclosure();
    const lastMessage = useRef(null)
    if (localStorage.getItem("app_key") == null) {
        localStorage.setItem("app_key", "chat")
    }
    return (<Flex
        direction="column"
        grow="1"
        maxWidth="100%"
        p={"100px 30px 0 30px"}
    >
        <ChatMessages scrollRef={lastMessage} messages={messages}/>
        <Chatbox scrollRef={lastMessage} messages={messages} setMessages={setMessages}/>

        <Menu>
            <MenuButton
                as={IconButton}
                aria-label="List options"
                icon={<HamburgerIcon/>}
                variant="primary"
                border={'1px solid #E2E8F0'}
                size="sm"
                mr={2}
                ml={2}
                w={"30px"}
                bg="blue.600"
                _hover={{bg: "blue.400"}}
                color={"white"}
                position={"fixed"}
                top={"70px"}
                right={"10px"}
            />
            <MenuList>
                <MenuItem onClick={onOpen}>
                    Open command history
                </MenuItem>
                <MenuItem onClick={()=>{
                    document.querySelectorAll(".response-box")
                        .forEach((e)=>{
                        // @ts-ignore
                            e.style.display = "none"
                    })
                }}>
                    Close all responses
                </MenuItem>
            </MenuList>
        </Menu>
        <CommandHistory
            isOpen={isOpen}
            onClose={onClose}
            onOpen={onOpen}
            scrollRef={lastMessage}
            setMessages={setMessages}
        />
    </Flex>)

}
