import Message from "./Message";
import {Flex, Text} from "@chakra-ui/react";
import {FieldValueTable} from "./FieldValueTable";
import {BtnProceed} from "./BtnProceed";
import {Command} from "./Chatbox";


export type RequestRender = {
    field_type: string;
    field_options?: Array<string>
}

enum ResponseRenderType {
    Chart = "chart",
    List = "list",
}

export type ResponseRenderDto = {
    render_type: ResponseRenderType
    fields?: Array<string>
}


export type TaskCommand = {
    speak: string;
    dataUpdate: any;
    command: Command
    chat: string
}

export function renderTaskCommand(task: TaskCommand, setMessages, scrollRef) {
    let dataUpdate = task.dataUpdate;
    let command = task.command;
    const speak = task.speak;
    let msgKey = Math.random();
    if (dataUpdate !== undefined && Object.values(dataUpdate).length > 0) {
        setMessages((prev: any[]) => {
            let message = (<Message sender="bot" key={msgKey} type="taskCommand">
                <Flex direction={"column"}>
                    <FieldValueTable
                        setMessages={setMessages}
                        command={command}
                        requestRender={task.command.request_render}
                        data={dataUpdate}
                        speak={speak}
                        scrollRef={scrollRef}
                    />
                </Flex>
            </Message>);
            return [...prev, message]
        });
        return
    }
    setMessages((prev: any[]) => {
        return [...prev, (
            <Message sender="bot" key={msgKey}>
                <Flex direction={"column"}>
                    <Text>{speak}</Text>
                    <BtnProceed command={command} commandData={dataUpdate} setMessages={setMessages} scrollRef={scrollRef}/>
                </Flex>
            </Message>
        )]
    });
}


export function setRenderInfo(renderInfoDto: ResponseRenderDto) {
    if (renderInfoDto == null) {
        return
    }
    localStorage.setItem("renderInfo", JSON.stringify(renderInfoDto))
}

export function getRenderInfo(): ResponseRenderDto {
    let item = localStorage.getItem("renderInfo");
    if (item == null) {
        return {render_type: ResponseRenderType.List}
    }
    return JSON.parse(item)
}

export function isRenderChart(): boolean {
    return getRenderInfo().render_type === ResponseRenderType.Chart;
}

export function updateChatboxWidth() {
    // @ts-ignore
    let width = document.querySelector(".chat-messages").offsetWidth + "px"
    // @ts-ignore
    document.querySelector(".chatbox").style.width = width;
}
