// React component
import {Input, Table, TableCaption, TableContainer, Tbody, Td, Textarea, Tfoot, Th, Thead, Tr} from "@chakra-ui/react"
import {useState} from "react";
import {BtnProceed} from "./BtnProceed";
import {Command} from "./Chatbox";
import {RequestRender} from "./Common";

type FieldValueTableProps = {
    data: any;
    speak: string
    setMessages: any
    command: Command
    requestRender?: Record<string, RequestRender>
    scrollRef: any
};

export function FieldValueTable(props: FieldValueTableProps): JSX.Element {
    const [args, setArgs] = useState(props.data)
    if (props.data.length === 0) {
        return (<div>No data</div>)
    }
    const onBlurHandler = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>): void => {
        const {
            name,
            value
        } = event.target
        // @ts-ignore
        setArgs(prevValues => {
            return ({
                ...prevValues!,
                [name]: value
            })
        })
        props.command.args.data_request = args
    }

    return (
        <>
            <TableContainer bg={"white"}>
                <Table variant='simple'>
                    <TableCaption>{props.speak}</TableCaption>
                    <Thead>
                        <Tr>
                            <Th>Field</Th>
                            <Th>Value</Th>
                        </Tr>
                    </Thead>
                    <Tbody>
                        {Object.keys(args).map(key => (
                            <Tr key={Math.random()}>
                                <Td>{key}</Td>
                                <Td>
                                    {!props.requestRender?.hasOwnProperty(key) &&
                                        (<Input name={key} defaultValue={args[key]} onBlur={onBlurHandler}/>)
                                    }
                                    {props.requestRender && props.requestRender[key]?.field_type == "password" &&
                                        (<Input type={"password"} name={key} defaultValue={args[key]} onBlur={onBlurHandler}/>)
                                    }
                                    {props.requestRender && props.requestRender[key]?.field_type == "email" &&
                                        (<Input type={"email"} name={key} defaultValue={args[key]} onBlur={onBlurHandler}/>)
                                    }
                                    {props.requestRender && props.requestRender[key]?.field_type == "input" &&
                                        (<Input name={key} defaultValue={args[key]} onBlur={onBlurHandler}/>)
                                    }
                                    {props.requestRender && props.requestRender[key]?.field_type == "checkbox" &&
                                        (<Input name={key} defaultValue={args[key]} onBlur={onBlurHandler}/>)
                                    }
                                    {props.requestRender && props.requestRender[key]?.field_type == "textarea" &&
                                        (<Textarea name={key} defaultValue={args[key]} onBlur={onBlurHandler}/>)
                                    }
                                </Td>
                            </Tr>
                        ))}
                    </Tbody>
                    <Tfoot>
                        <Tr>
                            <Th>Field</Th>
                            <Th>Value</Th>
                        </Tr>
                    </Tfoot>
                </Table>
            </TableContainer>
            <BtnProceed command={props.command} commandData={args} setMessages={props.setMessages} scrollRef={props.scrollRef}/>
        </>
    )
}
