import {Box, Button, Flex, FormControl, FormLabel, Heading, Input, Spinner} from '@chakra-ui/react';
import {useState} from 'react';
import {useRestAPI} from "../../hooks/useFetch";
import {useChatContext} from "../../hooks/useChatContext";
import { useNavigate } from 'react-router-dom';

const SignupPage = () => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const {loading, post} = useRestAPI(process.env.REACT_APP_SERVER_URL)
    const {dispatch} = useChatContext()

    let navigate = useNavigate()

    const login = async () => {
        const resp = await post("/admin/user/login", {
            "email": email,
            "password": password,
        })

        // @ts-ignore
        if (resp.status !== 200 || resp.data?.access_token == null) {
            alert("Login failed")
            return
        }
        dispatch({type: "LOGIN", payload: email})
        // @ts-ignore
        localStorage.setItem("token", resp.data.access_token);
        navigate("/chat")
    };

    const handleSubmit =async (event) => {
        event.preventDefault();
        if (name == '' || email == '' || password == '') {
            alert("Please fill all fields")
        }

        const resp = await post("/admin/user/register", {
            "email": email,
            "password": password,
            "name": name
        })

        if (resp.status !== 200) {
            alert("Signup failed")
            return
        }

        await login()
    };

    return (
        <Flex justifyContent="center" alignItems="center" height="100vh">
            <Box maxW="400px" px={6} py={8} shadow="md" borderRadius="md" bg="white">
                <Heading as="h1" size="xl" mb={6} textAlign="center">
                    Sign Up
                </Heading>
                <form onSubmit={handleSubmit}>
                    <FormControl id="name" mb={4}>
                        <FormLabel>Name</FormLabel>
                        <Input
                            type="text"
                            placeholder="Enter your name"
                            value={name}
                            onChange={(event) => setName(event.target.value)}
                        />
                    </FormControl>
                    <FormControl id="email" mb={4}>
                        <FormLabel>Email</FormLabel>
                        <Input
                            type="email"
                            placeholder="Enter your email"
                            value={email}
                            onChange={(event) => setEmail(event.target.value)}
                        />
                    </FormControl>
                    <FormControl id="password" mb={6}>
                        <FormLabel>Password</FormLabel>
                        <Input
                            type="password"
                            placeholder="Enter your password"
                            value={password}
                            onChange={(event) => setPassword(event.target.value)}
                        />
                    </FormControl>
                    {loading && <Spinner/>}
                    {!loading && (<Button type="submit" colorScheme="teal" size="lg" width="100%">
                        Sign Up
                    </Button>)}
                </form>
            </Box>
        </Flex>
    );
};

export default SignupPage;
