// React component
import {Button} from "@chakra-ui/react"
import {useRestAPI} from "../../hooks/useFetch";
import {useChatContext} from "../../hooks/useChatContext";
import {Command} from "./Chatbox";
import Message from "./Message";
import {MessageExpander} from "./MessageExpander";
import {RenderDataTable} from "./RenderDataTable";
import {isRenderChart} from "./Common";
import {RenderChart} from "./RenderChart";


type BtnProceedProps = {
    command: Command;
    setMessages: any;
    commandData: any
    scrollRef: any
};

export function BtnProceed(props: BtnProceedProps) {
    const {loading, makeRequest} = useRestAPI("");

    const apiCall = async (command: Command) => {
        console.log(command)
        try {
            prepareCommand(command);
            let response = await makeRequest(command.args.url, {
                method: command.args.method,
                headers: command.args.headers,
                body: props.commandData
            });
            let data = response.data;
            commandResponse(response);
            setAccessToken(data);
            return data;
        } catch (error) {
            console.error(error);
        }
    }
    const commandResponse = (resp: any) => {
        let displayText = "Action executed successfully.";
        if (resp.error?.status >= 400) {
            displayText = "Action failed.";
        }
        props.setMessages((prev: any[]) => {
            return [...prev, (
                <Message sender="bot" key={Math.random()}>
                    {isRenderChart() && Array.isArray(resp.data) && <MessageExpander content={<RenderChart data={resp.data}/>} btnText={"Show response"} displayText={displayText}/>}
                    {!isRenderChart() &&<MessageExpander content={<RenderDataTable data={resp.data}/>} btnText={"Show response"} displayText={displayText}/>}
                </Message>
            )]
        });
        props.scrollRef.current?.scrollIntoView({behavior: "smooth"});
    }
    const executeCommand = async () => {
        props.command.args.data_request = props.commandData

        if (props.command.name === 'browse_website') {
            let url = props.command.args.url.split('?')[0];
            url += "?" + Object.keys(props.command.args.data_request).map((key) => {
                return key + "=" + props.command.args.data_request[key];
            }).join("&");
            window.open(url, '_blank');
            return;
        }

        if (props.command.name === 'api_call') {
            const resp = await apiCall(props.command);
            console.log(resp)
        }
    };

    return (
        <Button onClick={executeCommand} width={"200px"} mt={"5px"} alignSelf={"end"}>
            {!loading && "Click to proceed"}
            {loading && "Processing..."}
        </Button>
    )
}

function setAccessToken(data: any) {
    if (typeof (data) != "object") {
        return data;
    }
    let dataObj = data as { [x: string]: string };
    Object.keys(dataObj).forEach((key) => {
        if (key === "access_token") {
            localStorage.setItem("token", dataObj[key]);
        }
    });
}

function handleAppKeySession(command: Command) {
    if (command.args.url.indexOf("user/session") >= 0) {
        localStorage.setItem("app_key", command.args.data_request.app_key);
    }
    if (command.args.url.indexOf("{app_key}") >= 0) {
        command.args.url = command.args.url.replace(
            "{app_key}",
            localStorage.getItem("app_key") ?? ""
        );
    }
    if (command.args.url.indexOf("<YOUR-APP-KEY>") >= 0) {
        command.args.url = command.args.url.replace(
            "<YOUR-APP-KEY>",
            localStorage.getItem("app_key") ?? ""
        );
    }
}

function handleAuthToken(command: Command) {
    Object.keys(command.args.headers).forEach((key) => {
        // Github app demo
        if (command.args.headers[key].indexOf("<YOUR-TOKEN>") >= 0 && command.args.url.indexOf("api.github.com") > 0) {
            command.args.headers[key] = "Bearer " + localStorage.getItem("github-token") ?? "";
            return
        }

        if (command.args.headers[key].indexOf("<YOUR-TOKEN>") >= 0) {
            command.args.headers[key] = "Bearer " + localStorage.getItem("token") ?? "";
        }
    });
}

function prepareCommand(command: Command) {
    if (typeof (command.args.headers) == "string") {
        command.args.headers = JSON.parse(command.args.headers);
    }
    handleAppKeySession(command);
    handleAuthToken(command);
}
