import * as React from "react"
import {useState} from "react"
import {Button, ChakraProvider, Grid, GridItem, theme} from "@chakra-ui/react"
import {Outlet} from "react-router-dom";
import Sidebar from "../components/Sidebar";
import NavBar from "../components/NavBar";
import PanelContent from "../components/PanelContent";
import {RequiresAuth} from "../components/RequiresAuth";
import { ArrowBackIcon, ArrowForwardIcon } from "@chakra-ui/icons";
import {updateChatboxWidth} from "../pages/chat/Common";

/**
 * Layout to be used for all pages related to admin pages. Eg. Key management, Users, etc.
 */
export const Chat = () => {
    const [isExpanded, setIsExpanded] = useState(false);

    const toggleView = () => {
        setIsExpanded(!isExpanded);
        setTimeout(() => {
            updateChatboxWidth();
        }, 100)
    };

    return (
        <RequiresAuth>
            <ChakraProvider theme={theme}>
                <NavBar/>
                <Grid templateColumns="repeat(6, 1fr)" bg="gray.50">
                    {/* sidebar */}
                    {!isExpanded && (
                        <GridItem
                            as="aside"
                            colSpan={{base: 6, lg: 2, xl: 2}}
                            bg="gray.50"
                            overflowY="scroll"
                            height={"100vh"}
                        >
                            <Sidebar/>
                        </GridItem>
                    )}

                    {/* main content & navbar */}
                    <GridItem
                        as="main"
                        colSpan={isExpanded ? {base: 6, lg: 6, xl: 6} : {base: 6, lg: 4, xl: 4}}
                        bg="white"
                        overflowY="scroll"
                        height={"100vh"}
                    >

                        <Button bg={"blue.600"}
                                zIndex={100000}
                                color={"white"}
                                aria-label={"Toggle sidebar"}
                                _hover={{bg: "blue.400"}}
                                ml={isExpanded ? "5px" : "-20px"}
                                mt={"80px"}
                                p={0}
                                position={"fixed"}
                                leftIcon={isExpanded ?  <ArrowForwardIcon/> : <ArrowBackIcon/>} onClick={toggleView}
                        />

                        <PanelContent variant="" id={"panel-content"}>
                            <Outlet/>
                        </PanelContent>
                    </GridItem>
                </Grid>
            </ChakraProvider>
        </RequiresAuth>
    )
}
