import {Drawer, DrawerBody, DrawerCloseButton, DrawerContent, DrawerHeader, DrawerOverlay, List, ListItem, Text} from "@chakra-ui/react";
import {useChatContext} from "../../hooks/useChatContext";
import {renderTaskCommand} from "./Common";

interface SideBarCommandListProps {
    isOpen: boolean;
    scrollRef: any;
    setMessages: any;

    onClose(): void;

    onOpen(): void;
}

export function CommandHistory(props: SideBarCommandListProps) {
    const {isOpen, onClose, scrollRef, setMessages} = props
    const context = useChatContext()

    return (
        <Drawer
            isOpen={isOpen}
            placement='right'
            onClose={onClose}
            finalFocusRef={scrollRef}
        >
            <DrawerOverlay/>
            <DrawerContent>
                <DrawerCloseButton/>
                <DrawerHeader>Command history</DrawerHeader>

                <DrawerBody>
                    <List spacing={1}>
                        {
                            context.commands.map((command, index) => {
                                return (
                                    <ListItem className={"prompt"} key={index} onClick={() => {
                                        renderTaskCommand(command, setMessages)
                                        onClose()
                                        scrollRef.current.scrollIntoView({behavior: "smooth"})
                                    }}>
                                        <Text>{command.chat}</Text>
                                    </ListItem>
                                )
                            })
                        }

                    </List>
                </DrawerBody>
            </DrawerContent>
        </Drawer>
    )
}
