import {Button, Flex, FormControl, Input, Spinner, Text} from "@chakra-ui/react"
import {SetStateAction, useEffect, useState} from "react"
import {useRestAPI} from "../../hooks/useFetch";
import {MessageExpander} from "./MessageExpander";
import Message from "./Message";
import {parseUrlParams} from "../../utils/common";
import {BtnProceed} from "./BtnProceed";
import {FieldValueTable} from "./FieldValueTable";
import {useChatContext} from "../../hooks/useChatContext";
import {renderTaskCommand, RequestRender, ResponseRenderDto, setRenderInfo, TaskCommand, updateChatboxWidth} from "./Common";

export type Command = {
    name: string;
    args: {
        headers: { [x: string]: string };
        url: string;
        method: any;
        data_request: any;
    };
    request_render?: Record<string, RequestRender>
    response_render?: ResponseRenderDto
};

// @ts-ignore
function parseCommandResponse(resp: any, chat: string): TaskCommand {
    let command = resp.data.command as Command;
    const speak = resp.data.thoughts?.speak as string;
    command.request_render = resp.data.request_render ?? command.request_render;
    command.response_render = resp.data.response_render ?? command.response_render;
    let taskCommand: TaskCommand = {dataUpdate: {}, command, speak, chat};
    console.log("taskCommand", taskCommand)
    return taskCommand
}

interface CommandError {
    errorCode: number,
    errorMessage: string
}

export function Chatbox(props: { scrollRef: any, setMessages: any, messages: any }) {
    const {scrollRef, setMessages, messages} = props
    const [chat, setChat] = useState("")
    const {loading, post} = useRestAPI(process.env.REACT_APP_SERVER_URL);
    const {dispatch, prompt} = useChatContext()

    useEffect(() => {
        setChat(prompt)
    }, [prompt])

    useEffect(() => {
        setMessages([<Message sender="bot" key={Math.random()}>
            <Text>How can I help you?</Text>
        </Message>]);
    }, [])

    const handleChange = (e: { target: { value: SetStateAction<string> } }) => {
        setChat(e.target.value)
    }


    async function getTaskCommand(docContext): Promise<TaskCommand | CommandError> {
        const resp = await post<Command>("/chat/completions", {
            "question": chat,
            "context": docContext,
        }, {
            "Authorization": "Bearer " + localStorage.getItem("token"),
        })
        console.log(resp)
        if (resp.error != null) {
            return {
                errorCode: resp.error.status,
                errorMessage: resp.error.statusText
            }
        }
        let taskCommand = parseCommandResponse(resp, chat);
        setRenderInfo(taskCommand.command.response_render)
        const command = taskCommand.command;
        if (command.name === 'browse_website') {
            taskCommand.dataUpdate = parseUrlParams(command.args.url);
            return taskCommand;
        }
        if (command.args.data_request == undefined) {
            taskCommand.dataUpdate = parseUrlParams(command.args.url);
            return taskCommand;
        }
        taskCommand.dataUpdate = command.args.data_request;
        if (typeof taskCommand.dataUpdate === 'string') {
            taskCommand.dataUpdate = JSON.parse(taskCommand.dataUpdate);
        }

        return taskCommand;
    }

    const sendMessage = async (e: React.MouseEvent<HTMLButtonElement>) => {
        e.preventDefault()
        if (chat !== "") {
            setMessages([...messages, (
                <Message key={Math.random()} sender="user">
                    <Text>{chat}</Text>
                </Message>
            )]);
        }
        scrollRef.current.scrollIntoView({behavior: "smooth"})
        let docContext = "";
        if (!chat.startsWith("++")) {
            let resp = await post("/docs/search", {
                "text": chat,
                "app_key": localStorage.getItem("app_key"),
            }, {
                "Authorization": "Bearer " + localStorage.getItem("token"),
            })
            console.log(resp)
            if (resp.error != null) {
                setMessages((prev: any[]) => {
                    return [...prev, (
                        <Message sender="bot" key={Math.random()}>
                            <Text>Something wrong try again later</Text>
                        </Message>
                    )]
                });
                scrollRef.current.scrollIntoView({behavior: "smooth"})
                return
            }
            // @ts-ignore
            let responseData = resp.data.data;
            if (responseData.length === 0) {
                setMessages((prev: any[]) => {
                    return [...prev, (
                        <Message sender="bot" key={Math.random()}>
                            <Text>Sorry I don't know what you mean</Text>
                        </Message>
                    )]
                });
                scrollRef.current.scrollIntoView({behavior: "smooth"})
                return
            }
            docContext = responseData[0].item_metadata.text;
        }
        setMessages((prev: any[]) => {
            return [...prev, (
                <Message sender="bot" key={Math.random()}>
                    <MessageExpander content={docContext} hide={true}/>
                </Message>
            )]
        });
        scrollRef.current.scrollIntoView({behavior: "smooth"})
        const task = await getTaskCommand(docContext);
        if ("errorCode" in task && task.errorCode != null) {
            let errorMessage = "Something wrong try again later";
            if (task.errorCode === 401) {
                errorMessage = "Please login again";
            }
            setMessages((prev: any[]) => {
                return [...prev, (
                    <Message sender="bot" key={Math.random()}>
                        <Text>{errorMessage}</Text>
                    </Message>
                )]
            });
            scrollRef.current.scrollIntoView({behavior: "smooth"})
            return
        }
        renderTaskCommand(task as TaskCommand, setMessages, scrollRef);

        scrollRef.current.scrollIntoView({behavior: "smooth"})
        dispatch({type: "SET_COMMAND", payload: task})
        setChat("")
    }

    setTimeout(() => {
        updateChatboxWidth();
    }, 100)
    return (
        <Flex className={"chatbox"} direction="row" position="fixed" width={"65%"} bottom={0} justifyContent={"center"} bg={"white"}>
            <FormControl
                p={2}
                pt={0}
                zIndex={3}
                as="form"
                display="flex"
                alignItems="center"
            >
                <Input
                    size="lg"
                    value={chat}
                    onChange={handleChange}
                    placeholder="Type Message"
                    bg={"white"}
                />
                <Button size="lg" type="submit" onClick={sendMessage}>
                    Send
                </Button>
            </FormControl>
        </Flex>
    )
}
