import {Box, useColorMode} from "@chakra-ui/react";

export default function Message(props:{ [x: string]: any; children: any; }) {
    const {sender, children} = props;
    const {colorMode} = useColorMode()
    const bgColor = {light: "gray.100", dark: "gray.600"}
    const textColor = {light: "black", dark: "white"}
    return (
        <Box
            bg={sender == "user" ? "blue.500" : bgColor[colorMode]}
            w={sender == "user" ? "fit-content":"90%"}
            py={1}
            px={3}
            rounded="xl"
            margin={2}
            ml={sender == "user" ? "auto" : "0"}
            position="relative"
            textAlign={sender == "user" ? "right" : "left"}
            wordBreak="break-word"
            color={sender == "user" ? "white" : textColor[colorMode]}
        >
            {children}
        </Box>
    )
}
